<template>
    <div>
        <el-dialog title="详细信息" :visible.sync="dialogpmisible" @close="closeDialog">

        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            info: {},
            dialogpmisible: false,
        }
    },
    props: {
        pmVisible: Boolean,
        pmitem: Object
    },
    watch: {
        pmVisible(newValue, oldValue) {
            this.dialogpmisible = newValue;
        },
        pmitem(newValue, oldValue) {
            this.info = newValue;
        }
    },
    methods: {
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        }
    }
}
</script>


