<template>
    <div>
        <el-main>
            <div class="select">
                <el-input v-model="id" placeholder="ID" clearable></el-input>
                <el-input v-model="name" placeholder="用户名" clearable></el-input>
            </div>
            <div class="btn">
                <el-button type="danger" icon="el-icon-delete-solid">批量删除</el-button>
                <el-button type="primary" icon="el-icon-document">导出</el-button>
            </div>
            <el-table
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'id', order: 'descending'}"
                :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
            >
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="id" label="ID" sortable></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="group" label="分组"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑权限</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                layout="prev, pager, next"
                :total="1000"
            ></el-pagination>
        </el-main>
        <pm :pmitem="pmitem" :pmVisible="pmVisible" @close-dialogStatus="Close_dialog"></pm>
    </div>
</template>

<script>
import pm from "@/components/dialog/pm";
export default {
    data() {
        return {
            pmitem: {},
            pmVisible: false,
            ip: "",
            time: "",
            name: "",
            tableData: [
                {
                    id: "12345",
                    name: "admin",
                    group: "超级管理员",
                    power: ""
                },
                {
                    id: "233333",
                    name: "kkk",
                    group: "管理员",
                    power: ""
                },
                {
                    id: "666666",
                    name: "aaa",
                    group: "管理员",
                    power: ""
                },
                {
                    id: "99999",
                    name: "ccccc",
                    group: "普通用户",
                    power: ""
                },
                {
                    id: "1564586845",
                    name: "CSfdsgfs",
                    group: "管理员",
                    power: ""
                },
                {
                    id: "43676587",
                    name: "eftrghy",
                    group: "普通用户",
                    power: ""
                }
            ]
        };
    },
    components: {
        pm
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        Close_dialog(val) {
            this.pmVisible = false;
        },
        handleEdit(index, row) {
            this.pmitem = row;
            this.pmVisible = true;
        },
        handleDelete(index, row) {
            this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$message({
                    type: "success",
                    message: "删除成功!"
                });
                for (let i in this.tableData) {
                    if (this.tableData[i] == row) {
                        this.tableData.splice(i, 1);
                    }
                }
            });
        }
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}

.select {
    display: inline;
}

.select, .el-input {
    width: 156px;
}
</style>


